.Title {
  background: #1a1818;
  height: 85px;
  text-align: left;
  margin-left: 25%;
  font-size: 1rem;
  font-family: "Rubik Broken Fax";
}

.NavbarItems {
  background: #1a1818;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  width: 100%;
  text-align: center;
  margin-left: 0;
  font-family: "Rubik Broken Fax";
}

.navbar-logo {
  color: rgb(202, 197, 227);
  justify-self: start;
  margin-left: 0px;
  margin-top: 20px;
  cursor: pointer;
  font-family: "Rubik Broken Fax";
}

.logo {
  width: 230px;
  font-family: "Rubik Broken Fax";
}

.nav-menu {
  font-family: "Rubik Broken Fax";
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 50px;
  list-style: none;
  text-align: left;
  justify-content: end;
  padding-top: 1rem;
  padding-left: 0rem;
  font-size: 25px;
}

.nav-links {
  font-family: "Rubik Broken Fax";
  color: rgb(207, 165, 165);
  text-decoration: none;
}

.nav-links-clicked {
  font-family: "Rubik Broken Fax";
  color: white;
  background-color: red;
  text-decoration: none;
  border-radius: 4px;
}

.nav-links:hover {
  font-family: "Rubik Broken Fax";
  color: white;
  background-color: red;
  text-decoration: none;
  border-radius: 4px;
  transition: all 0.2s ease-out;
}
